import Mock from "mockjs";
var mock = Mock.Random,arr=[];
for(var i = 0;i<10;i++){
    var num = mock.integer(5,10);
    var oldNum = num;
    var obj = {
        id:mock.guid(),
        title:mock.ctitle(),
        info:mock.cparagraph(),
        img:mock.dataImage("200x100"),
        price:mock.integer(1,100),
        num:num,
        oldNum
    }
    arr.push(obj)
}
const list = {
    namespaced: true,
    state: {
        arr,
        carlist:[]
    },
    mutations: {
        add(state,id){
            var item = state.arr.find(ele => ele.id == id);
            var eles = state.carlist.find(ele => ele.id == id);
            var obj = {
                id:id,
                title:item.title,
                num:1,
                price:item.price,
                isChecked:false,
                maxVal:item.oldNum
            };
            
            if(item.num <=0){

            }else{
                item.num -- ;
                if(eles){
                    eles.num ++;
                }else{
                    state.carlist.push(obj);
                }
            }
            
        },
        upt(state,{id , val}){ // 1 -1 // val 购物车改变之后的值
            var item = state.arr.find(ele => ele.id == id);
            var eles = state.carlist.find(ele => ele.id == id);
            item.num = item.oldNum - val ;
            // if(val >0){
            //     if(item.num >0){
            //         eles.num += val;
            //         item.num -= val;
            //     }else{

            //     }
            // }else{
            //     if(eles.num >0){
            //         eles.num = eles.num + val;
            //         item.num = item.num - val;
            //     }else{

            //     }
            // }
            
            
        }
    },
    actions: {
        toggle({getters,state}){
            if(getters.isAllChecked){
                state.carlist.forEach(ele => ele.isChecked = false)
            }else{
                state.carlist.forEach(ele => ele.isChecked = true)
            }
        }
    },
    getters: {
        total(state){
            return state.carlist.reduce((total, current)=>{
                return total =  total + current.num
            },0)
        },
        isAllChecked(state){
            if(state.carlist.length == 0){
                return false
            }
            return state.carlist.every(ele => ele.isChecked == true);
        },
        totalPrice(state){
            return state.carlist.reduce((total, current)=>{
                if(current.isChecked){
                    return total =  total + current.num*current.price
                }else{
                    return total 
                }
                
            },0)
        }
    }
}
export default list;