<template>
  <div id="app">
    <div class="main">
      <transition :enter-active-class="currentClass">
        <router-view/>
      </transition>
      
    </div>
    <van-tabbar route v-if="$route.meta.isFootShow">
      <van-tabbar-item
        replace
        to="/"
        icon="wap-home-o"
      >
        Home
      </van-tabbar-item>
      <van-tabbar-item
        replace
        to="/car"
        icon="shopping-cart-o"
      >
        购物车
      </van-tabbar-item>
      <van-tabbar-item
        replace
        to="/about"
        icon="manager-o"
      >
        About
      </van-tabbar-item>
    </van-tabbar>
    <!-- <div id="nav" v-if="$route.meta.isFootShow">
      <router-link to="/">Home </router-link> 
      <router-link to="/car">购物车</router-link>
      <router-link to="/about">About</router-link>
    </div> -->
    
  </div>
</template>
<script>
export default {
  data () {
    return {
      currentClass:""
    }
  },
  watch: {
    "$route":function(to, from ){
      var old = from.path.split("/").length;
      var current = to.path.split("/").length;
      if(current > old){
        this.currentClass = "animated fadeInRight"
      }else if(current == old){
        this.currentClass = "animated fadeIn"
      }else{
        this.currentClass = "animated fadeInLeft"
      }
    }
  }
}
</script>
